<template>
  <div class="article">
    <!-- 文章图片 -->
    <div class="article__image" :style="imageUrl"></div>
    <!-- 文章标题 -->
    <div class="article__title" v-text="title"></div>
    <!-- 文章标签 -->
    <div class="article__description" v-text="description"></div>
    <!-- 文章信息 -->
    <div class="article__info">
      <div class="tags">
        <Tag v-for="value,  index in tags" :key="index" :skillType="value"></Tag>
      </div>
      <span class="article__date" v-text="article_date"></span>
    </div>
  </div>
</template>

<script>
import Tag from "./Tag.vue";
const ARTICLE_DEFAULT_URL = "image_p.webp";

export default {
  name: "Article",
  data() {
    return { 
    }
  },
  computed: {
    imageUrl(){
      return {
        backgroundImage: `url(${require('/src/assets/images/'+ this.imgName +'')})`
      }
    }
  },
  components: {
    Tag
  },
  props: {
    imgName: {
      default: ARTICLE_DEFAULT_URL,
      type: String
    },
    description: {
      default: "默认",
      type: String
    },
    title: {
      default: "默认",
      type: String
    },
    tags: {
      default: [],
      type: Array
    },
    article_date: {
      default: "00-00-00",
      type: Date
    }
  }
}
</script>

<style scoped>
  .article {
    display: grid;
    grid-template: 
      "left   title"
      "left   description"
      "left   info" / 13rem auto;
    column-gap: 2rem;
    height: 13rem;
    /* width: 100px; */
  }

  .article__image {
    width: 13rem;
    height: 13rem;
    background: black;
    border-radius: 20px;
    /* border: 2px solid darkgray; */
    grid-area: left;
    /* background: url("../../assets/images/image_article.webp"); */
    background-size: contain;
    /* background-image: "../../assets/images/image_article.webp"; */
  }

  .article__title {
    font-size: 20px;
    font-weight: bolder;
  }

  .article__description {
    color: gray;
    font-size: 1.5rem;
    text-overflow: ellipsis;
    overflow: hidden;
    height: 6.5rem;
  }

  .article__info {
    padding-right: 2rem;
    justify-self: right;
    align-content: center;
  }

  .article__info > div {
    display: inline-block;
  }

  .article__date {
    font-size: 1rem;
    margin-left: 2rem;
  }

  .tag {
    background: var(--color-background);
    color: white;
    display: inline-block;
    margin: 0 0.2rem;

  }

  
</style>