<template>
    <div class="page">
      <div class="icon">PAGE</div>
      <div class="page__controller">
        <span class="privous" @click="currentPage--">上一页</span>
        <span class="next" @click="currentPage++">下一页</span>
      </div>
      <div class="page__total">
				第 <span v-if="currentPage<10">0</span>{{ currentPage }}页 / 
				共{{ totalPage }}页</div>
    </div>
</template>

<script>
export default {
  name: "PageController",
  data() {
    return {
      currentPage: 1,
      totalPage: 20
    }
  },
	watch: {
		// 检测当前页码是否越界， 比如-1或者超过额定的文章数目
		currentPage(newV) {
			if(newV <= 0){
				this.currentPage=1;
			}
			if(newV > this.totalPage){
				this.currentPage=this.totalPage;
			}
		}
	}

}
</script>

<style scoped>
  .page {
    width: 50%;
    padding: 0 2rem;
    margin: 0 auto;
    line-height: 4rem;
    justify-content: space-between;
    height: 4rem;
    flex-direction: row;
    display: flex;
    color: white;
    box-sizing: border-box;
    border-radius: 10px;
    background: var(--color-backbround-primary);
    user-select: none;
  }

  .page__controller > span {
    display: inline-block;
    padding: 0 2rem;
  }

  .page__controller > span:hover {
    cursor: pointer;
    text-decoration: underline;
  }
</style>